@import url(https://fonts.googleapis.com/css?family=Roboto:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


.header {
  background-color: #222;
  padding: 1.5625em 0;
  text-align: center;
}

.header img {
  width: 100px;
}
.tesla-battery {
  width: 70em;
  margin: 0 auto;
}

.tesla-battery h1 {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 2.25rem;
  text-align: center;
  letter-spacing: 0.0833em;
}

.tesla-climate-container {
  float: left;
  width: 420px;
  padding: 0 40px;
  margin: 0 40px 0 0;
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
}

.tesla-controls {
  display: block;
  width: 100%;
}
.tesla-battery__notice {
  margin: 10em 0;
  color: #666;
  line-height: 1.25;
}
.tesla-car {
  width: 100%;
  min-height: 25em;
  background: #fff url(/static/media/tesla.ff16203f.jpg) no-repeat top center;
  background-size: contain;
}

.tesla-wheels {
  position: relative;
  width: 37.5em;
  height: 17.5em;
  margin: 0 auto;
}

.tesla-wheel {
  height: 5em;
  width: 5em;
  bottom: 0;
  position: absolute;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;
}

.tesla-wheel--front {
  left: 3.125em;
}

.tesla-wheel--rear {
  right: 4.375em;
}

.tesla-wheel--19 {
  background-image: url(/static/media/19.c8ef3398.svg);
  -webkit-animation: infinite-spinning 750ms linear infinite;
          animation: infinite-spinning 750ms linear infinite;
}

.tesla-wheel--21 {
  background-image: url(/static/media/21.a52693ae.svg);
  -webkit-animation: infinite-spinning 1s linear infinite;
          animation: infinite-spinning 1s linear infinite;
}

@-webkit-keyframes infinite-spinning {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes infinite-spinning {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}


.tesla-stats {
  margin: -5em 0 2em;
}

.tesla-stats ul {
  text-align: center;
  list-style: none;
}

.tesla-stats ul li {
  display: inline-block;
  width: 10em;
  position: relative;
}

.tesla-stats ul li p {
  font-size: 2.5rem;
  font-weight: 400;
  font-family: 'roboto';
  display: block;
  padding: 0 1em 0 0;
  position: relative;
  color: #008dff;
  text-align: right;
}

.tesla-stats ul li p:after {
  font-size: 1rem;
  content: 'MI';
  position: absolute;
  top: 0.5em;
  right: 0;
}

.tesla-stats-icon {
  height: 1.25em;
  background-size: auto 1em;
  background-position: top right;
  background-repeat: no-repeat;
}

.tesla-stats-icon.tesla-stats-icon--60 {
  background-image: url(/static/media/60.7ee66eea.svg);
}

.tesla-stats-icon.tesla-stats-icon--60d {
  background-image: url(/static/media/60d.246b5ba5.svg);
}

.tesla-stats-icon.tesla-stats-icon--75 {
  background-image: url(/static/media/75.37862a62.svg);
}

.tesla-stats-icon.tesla-stats-icon--75d {
  background-image: url(/static/media/75d.9c937cb9.svg);
}

.tesla-stats-icon.tesla-stats-icon--90d {
  background-image: url(/static/media/90d.28a215f0.svg);
}

.tesla-stats-icon.tesla-stats-icon--p100d {
  background-image: url(/static/media/p100d.62ed87b4.svg);
}


.tesla-counter {
  float: left;
  width: 230px;
}

.tesla-counter__title {
  letter-spacing: 2px;
}

.tesla-counter__container {
  margin: 10px 0 0 ;
  padding-right: 40px;
}

.tesla-counter__container input[type=number] {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.tesla-counter__number {
  font-family: 'roboto';
  font-size:1.5rem;
  line-height: 1;
  position: relative;
}

.tesla-counter__number span {
  position: absolute;
  top: 0;
  left: 2em;
  font-size: 1rem;
  text-transform: uppercase;
}

.tesla-counter__item {
  position: relative;
  width: 100%;
  height: 4em;
  border: 1px solid #ccc;
  padding: 0.5em 1em 0.5em 2em;
  margin: 0 0.5em 0 0;
  background-color: #f7f7f7;
  background-position: 25% 0.5em;
  background-size: 3em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.tesla-counter__item:focus {
  background-color: #f2f2f2;
  outline: none;
}

/* .tesla-counter__controls {
  position: absolute;
  right: 0.625em;
  top: 0.5em;
} */

.tesla-counter__controls button {
  outline: 0;
  width: 2em;
  color: #008dff;
  display: block;
  padding: 0.75em 0;
  border: 0;
  background-size: 60%;
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  cursor: pointer;
}

.tesla-counter__controls button[disabled] {
  opacity: 0.4;
  cursor: not-allowed;
}

.tesla-counter__controls button:first-child {
  border-bottom: 1px solid #fff;
  background-image: url(/static/media/up.b3fc73bf.svg)
}

.tesla-counter__controls button:last-child {
  border-top: 1px solid #ccc;
  background-image: url(/static/media/down.a56566ba.svg)
}
.tesla-climate {
  float: left;
}

.tesla-climate__item {
  cursor: pointer;
  display: block;
  width: 6.25em;
  height: 6.25em;
  border: 6px solid #f7f7f7;
  border-radius: 50%;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3);
  color: #666;
  background: #fff;
}

.tesla-climate__item--active {
  color: #fff;
  background: #33a0ff;
  background: linear-gradient(top, #33a0ff 0%, #388bff 100%);
}

.tesla-climate__item--active.tesla-heat {
  background: #d64800;
  background: linear-gradient(top, #d64800 0%, #d20200 100%);
}

.tesla-climate__icon {
  display: block;
  width: 1.5em;
  height: 1.5em;
  margin: 0.5em auto 0;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(/static/media/ac-off.c418a045.svg);
}

.tesla-heat .tesla-climate__icon {
  background-image: url(/static/media/heat-off.987170ea.svg);
}

.tesla-climate__item--active .tesla-climate__icon {
  background-image: url(/static/media/ac-on.4dcccadf.svg);
}

.tesla-climate__item--active.tesla-heat .tesla-climate__icon {
  background-image: url(/static/media/heat-on.2c61b1a6.svg);
}

.tesla-climate p {
  margin: 1em 0 0;
  text-align: center;
  font-size: 0.625rem;
  text-transform: uppercase;
}

.tesla-climate input[type="checkbox"] {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.tesla-wheels-option {
  float: left;
  width: 25em;
}

.tesla-wheels__title {
  letter-spacing: 2px;
}

.tesla-wheels__container {
  margin: 0.625em 0 0;
}

.tesla-wheels__item {
  cursor: pointer;
  width: 47.5%;
  height: 4em;
  border: 1px solid #ccc;
  display: inline-block;
  padding: 1.25em 0 0 6em;
  margin: 0 0.5em 0 0;
  background-color: #f7f7f7;
  background-position: 25% 0.5em;
  background-repeat: no-repeat;
  background-size: 3em;
}

.tesla-wheels__item--19 {
  background-image: url(/static/media/19.c8ef3398.svg);
}

.tesla-wheels__item--21 {
  background-image: url(/static/media/21.a52693ae.svg);
}

.tesla-wheels__item--focused {
  background-color: #f2f2f2;
}

.tesla-wheels__item--active {
  border-color: #39f;
  box-shadow: inset 0px 0px 0px 1px #39f;
}

.tesla-wheels__item p {
  font-family: 'roboto';
  color: #333;
}

.tesla-wheels__item input[type="radio"] {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: 1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
